body {
  background-color: rebeccapurple;
}
.picture-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}

.image-container {
  overflow: hidden;
  height: 40vh;
}

.image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.3s ease;
}

.image-container img:hover {
  transform: scale(1.25);
}

.info {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
}

button {
  border-radius: 10px;
  padding: 0.25rem 0.5rem;
  font-size: 1rem;
  border: none;
  background-color: #1c58a7;
  color: white;
  cursor: pointer;
}
.title {
  text-align: center;
}

